<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
import config from "../../../../public/config/customSetting.json";

export default {
  name: "consentCensor-component",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      cst4CensorTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "create_date", label: "申請日期", width: "150" },
        { id: "personInfo", label: "姓名(性別)身分證字號", width: "250" },
        { id: "s_Id", label: "廠商統編", width: "150" },
        { id: "file_Name", label: "附檔", width: "" },
      ],

      approveVisible: false, // 同意視窗
      approveItem: [],
      rejectVisible: false, // 退回視窗
      rejectItem: [],
      rejectReason: "", // 退件說明
    };
  },
  computed: {
    // 待審核個人同意書資料 (All)
    cst4Censor() {
      return this.$store.state.cst_forcensor;
    },
    // 待審核個人同意書資料 (單頁)
    cst4CensorList() {
      return this.$store.state.cst_forcensorlist;
    },
  },
  methods: {
    // 從對應 controller 下載文件，並指定檔名
    async downloadPdf(item) {
      try {
        const res = await this.$store.dispatch("commonDownloadPdf", {
          file_id: item.file_Id,
          router_Name: config.getPrivacyZone,
        });
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", `${item.file_Name}`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },

    // 開啟視窗，並代入相應的資訊
    approveDialog(item) {
      this.approveVisible = true;
      this.approveItem = this.getItem(item);
    },
    rejectDialog(item) {
      this.rejectVisible = true;
      this.rejectItem = this.getItem(item);
    },
    handleClose(done) {
      this.$confirm("確認關閉？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    getItem(item) {
      let data = {};
      data.id = item.id;
      data.p_Name = item.p_Name;
      data.sex = item.sex;
      data.s_Id = item.s_Id;
      data.insurance_Begin = item.insurance_Begin;
      data.insurance_End = item.insurance_End;
      data.approve_Status = "";
      data.ai_No = item.ai_No;
      data.reject_Reason = "";
      data.create_Date = item.create_Date;
      data.name = "";
      data.file_Id = "";
      data.file_Name = "";
      data.s_Name = "";
      return [data];
    },

    async approveCensor() {
      try {
        const res = await this.$store.dispatch("approveCst", this.approveItem);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "審核成功",
          });

          // 關閉視窗，並更新待審核清單 (呈現第一頁)
          this.approveVisible = false;
          this.$store.commit("CURRENT_PAGE_INIT");
        }
      } catch (err) {
        console.log(err);
      }
    },
    async rejectCensor() {
      if (!this.rejectReason) {
        this.$message({
          type: "error",
          showClose: true,
          message: "請提供退件原因",
        });
      } else {
        this.rejectItem[0].reject_Reason = this.rejectReason;
        try {
          const res = await this.$store.dispatch("rejectCst", this.rejectItem);
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "審核成功",
            });

            // 清空表單、關閉視窗，並更新待審核清單 (呈現第一頁)
            this.rejectVisible = false;
            this.rejectReason = "";
            this.$store.commit("CURRENT_PAGE_INIT");
          }
        } catch (err) {
          console.log(err);
        }
      }
    },

    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "cst_forcensor",
        paramsListName: "cst_forcensorlist",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得資料，呈現表格第一頁
    try {
      await this.$store.dispatch("getCstForCensor");
    } catch (err) {
      console.log(err);
    }
    this.$store.commit("CURRENT_PAGE_INIT");
  },
};
</script>

<template src="./consentCensor.html"></template>
